import React, { useState } from 'react';
import './App.css';
import './Products.css';
import './Nav.js'
import Cart from './Cart';
import iphone11 from './images/productimg/iphone11.jpg';
import iphone7 from './images/productimg/iphone7.jpg';
import galaxya71 from './images/productimg/galaxya71.jpg';
import galaxys20 from './images/productimg/galaxys20+.jpg';



function Products() {
  const [cart, setCart] = useState([]);
  
  const [products] = useState([
    {
      name: 'Apple iPhone 11',
      cost: '$900.00',
      image: <img alt="iphone11" src={require('./images/productimg/iphone11.jpg')} />
    },
    {
      name: 'Apple iPhone 7',
      cost: '$500.00',
      image: <img src={iphone7} alt="iphone7" height="500px" width="500px"/>
    },
    {
      name: 'Samsung Galaxy A71',
      cost: '$350.00',
      image: {galaxya71}
    },
    {
      name: 'Samsung Galaxy S20+',
      cost: '$850.00',
      image: {galaxys20}
    }
  ]);

  const addToCart = (product) => {
    console.log('we are in cart');
    setCart([...cart, product]);
  };



  return (
    <div className="main">
    <header>
    <button className="cart2">Cart ({cart.length})</button>
    </header>
    <div className="Container">
      {products.map((product, idx) => (
        <div className="product" key={idx}>
          <img src={product.image} alt={product.name} />
          <h3>{product.name}</h3>
          <h4>{product.cost}</h4>
          <button onClick={() => addToCart(product)}>
            Add to Cart
            </button>
        </div>
      ))}
    </div>
    </div>
    
    
  );
}

export default Products;
